// --- Constants ---
const ALL_ACTIVITIES = ["Exercise", "Social", "Work", "Hobby", "Rest", "Nutrition"];

// --- Type Definitions ---
type AnalysisResult = {
  sentiment: 'POSITIVE' | 'NEGATIVE' | 'NEUTRAL' | string;
  labels: string[];
  activities: string[];
};

type StreakData = {
  streakCount: number;
  lastJournalDate: string | null;
};

type View = 'journal' | 'dashboard'; // Type for managing tabs

// --- Helper Functions ---
const getISODate = (date: Date): string => {
  return date.toISOString().split('T')[0];
};

// ==================================================================
// 1. STRATEGY ENGINE (from strategyEngine.ts)
// ==================================================================
interface Strategy {
  title: string;
  text: string;
  emoji: string;
  requiredTags: string[];
}

const strategyLibrary: Strategy[] = [
  { title: "Feeling Anxious about Work?", text: "Try the 'Urgent-Important Matrix' to organize your tasks. Focusing on what's truly important can reduce feelings of being overwhelmed.", emoji: "📋", requiredTags: ["Anxiety", "Work"] },
  { title: "Feeling Down about Relationships?", text: "Consider writing a short, positive message to a friend or family member. Expressing gratitude can strengthen bonds and lift your spirits.", emoji: "💌", requiredTags: ["Sadness", "Relationships"] },
  { title: "Feeling Fatigued?", text: "Your energy seems low. A short, 10-minute walk can boost circulation and mental clarity more effectively than staying seated.", emoji: "🚶‍♀️", requiredTags: ["Fatigue"] },
  { title: "Feeling Anxious?", text: "When you feel overwhelmed, try the 4-7-8 breathing technique. Inhale for 4s, hold for 7s, and exhale for 8s to calm your nervous system.", emoji: "🌬️", requiredTags: ["Anxiety"] },
  { title: "Feeling Sad?", text: "It's okay to feel this way. Try writing down three small things you are grateful for today. This practice can gently shift your perspective.", emoji: "❤️", requiredTags: ["Sadness"] },
];

export function getPersonalizedStrategy(analysis: AnalysisResult): Strategy {
  let bestMatch: Strategy | null = null;
  let highestScore = 0;
  const allUserTags = [...analysis.labels, ...analysis.activities];

  for (const strategy of strategyLibrary) {
    const score = strategy.requiredTags.reduce((acc, tag) => {
      return allUserTags.includes(tag) ? acc + 1 : acc;
    }, 0);
    if (score > highestScore) {
      highestScore = score;
      bestMatch = strategy;
    }
  }
  return bestMatch || { title: "Your Daily Insight ✨", text: "Reflecting on your feelings is a powerful step. Notice the patterns in your thoughts and activities.", emoji: "🧠", requiredTags: [] };
}


