// --- BreathingExercise.tsx ---
const breathingPhases = [
  { name: "Inhale", duration: 4 },
  { name: "Hold", duration: 7 },
  { name: "Exhale", duration: 8 },
];
const breathingTotalCycles = 4;

function BreathingExercise({ onClose }: { onClose: () => void }) {
  const [cycleIndex, setCycleIndex] = useState(-1);
  const [instruction, setInstruction] = useState("Get Ready");
  const [countdown, setCountdown] = useState(3);

  useEffect(() => {
    const interval = setInterval(() => {
      setCountdown(c => c - 1);
    }, 1000);
    if (countdown === 0) {
      const nextCycleIndex = cycleIndex + 1;
      if (nextCycleIndex >= breathingPhases.length * breathingTotalCycles) {
        clearInterval(interval);
        onClose();
        return;
      }
      const nextPhase = breathingPhases[nextCycleIndex % breathingPhases.length];
      setCycleIndex(nextCycleIndex);
      setInstruction(nextPhase.name);
      setCountdown(nextPhase.duration);
    }
    return () => clearInterval(interval);
  }, [countdown, cycleIndex, onClose]);

  return (
    <div className="fixed inset-0 bg-indigo-900 bg-opacity-90 flex flex-col items-center justify-center z-50 animate-fade-in">
      <div className="text-center">
        <div className="w-64 h-64 rounded-full border-4 border-white flex items-center justify-center">
          <div>
            <p className="text-white text-4xl font-bold">{instruction}</p>
            <p className="text-white text-8xl font-bold mt-2">{countdown}</p>
          </div>
        </div>
      </div>
      <button onClick={onClose} className="absolute top-4 right-6 text-white text-3xl font-bold opacity-70 hover:opacity-100">&times;</button>
    </div>
  );
}

// --- GoalTracker.tsx ---
interface Habit { id: string; name: string; target: number; progress: number; emoji: string; }
const initialHabits: Habit[] = [
  { id: 'exercise', name: 'Exercise', target: 3, progress: 0, emoji: '💪' },
  { id: 'mindful', name: 'Mindful Minutes', target: 5, progress: 0, emoji: '🧘' },
  { id: 'hydrate', name: 'Hydrate (8 glasses)', target: 7, progress: 0, emoji: '💧' },
];

function GoalTracker() {
  const [habits, setHabits] = useState<Habit[]>([]);

  useEffect(() => {
    if (typeof window === 'undefined') return;
    const savedHabits = localStorage.getItem('aura-habits');
    setHabits(savedHabits ? JSON.parse(savedHabits) : initialHabits);
  }, []);

  useEffect(() => {
    if (typeof window === 'undefined' || habits.length === 0) return;
    localStorage.setItem('aura-habits', JSON.stringify(habits));
  }, [habits]);

  const handleProgress = (id: string) => {
    setHabits(habits.map(h => (h.id === id && h.progress < h.target) ? { ...h, progress: h.progress + 1 } : h));
  };
  const resetProgress = () => {
    const resetHabits = habits.map(h => ({ ...h, progress: 0 }));
    setHabits(resetHabits);
  };

  return (
    <div className="bg-white p-6 rounded-2xl shadow-lg">
      <div className="flex justify-between items-center mb-4">
        <h2 className="text-2xl font-bold text-slate-800">This Week's Goals</h2>
        <button onClick={resetProgress} className="text-sm text-indigo-500 hover:underline">Reset</button>
      </div>
      <div className="space-y-4">
        {habits.map(habit => (
          <div key={habit.id}>
            <div className="flex justify-between items-center"><span className="font-semibold">{habit.emoji} {habit.name}</span><span>{habit.progress} / {habit.target}</span></div>
            <div className="w-full bg-slate-200 rounded-full h-2.5 mt-1"><div className="bg-indigo-500 h-2.5 rounded-full" style={{ width: `${(habit.progress / habit.target) * 100}%` }}></div></div>
            <button onClick={() => handleProgress(habit.id)} className="mt-2 text-sm bg-slate-200 px-3 py-1 rounded-full hover:bg-slate-300">Log +1</button>
          </div>
        ))}
      </div>
    </div>
  );
}

// --- WeeklySummary.tsx ---
interface HistoryEntry { date: string; sentiment: string; labels: string[]; activities: string[]; }
interface SummaryData { totalEntries: number; moods: { [key: string]: number }; topEmotion: string; topActivity: string; }

function WeeklySummary() {
  const [showModal, setShowModal] = useState(false);
  const [summary, setSummary] = useState<SummaryData | null>(null);

  const calculateSummary = () => {
    if (typeof window === 'undefined') return;
    const history: HistoryEntry[] = JSON.parse(localStorage.getItem('aura-history') || '[]');
    const sevenDaysAgo = new Date();
    sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
    const recentEntries = history.filter(entry => new Date(entry.date) > sevenDaysAgo);

    if (recentEntries.length === 0) {
      setSummary({ totalEntries: 0, moods: {}, topEmotion: 'N/A', topActivity: 'N/A' });
      return;
    }
    const moodCounts: { [key: string]: number } = { POSITIVE: 0, NEGATIVE: 0, NEUTRAL: 0 };
    const labelCounts: { [key: string]: number } = {};
    const activityOnPositiveDays: { [key: string]: number } = {};

    recentEntries.forEach(entry => {
      if (moodCounts[entry.sentiment] !== undefined) moodCounts[entry.sentiment]++;
      entry.labels.forEach(label => { labelCounts[label] = (labelCounts[label] || 0) + 1; });
      if (entry.sentiment === 'POSITIVE') entry.activities.forEach(act => { activityOnPositiveDays[act] = (activityOnPositiveDays[act] || 0) + 1; });
    });

    const getTopItem = (obj: { [key: string]: number }) => Object.keys(obj).reduce((a, b) => obj[a] > obj[b] ? a : b, 'N/A');
    setSummary({ totalEntries: recentEntries.length, moods: moodCounts, topEmotion: getTopItem(labelCounts), topActivity: getTopItem(activityOnPositiveDays) });
  };

  const handleShowSummary = () => { calculateSummary(); setShowModal(true); };

  const MoodStat = ({ mood, count, total }: { mood: string, count: number, total: number }) => {
    const percentage = total > 0 ? Math.round((count / total) * 100) : 0;
    const colors: { [key:string]: string } = { POSITIVE: 'bg-green-500', NEGATIVE: 'bg-rose-500', NEUTRAL: 'bg-slate-500' };
    return (
      <div>
        <div className="flex justify-between font-semibold text-sm"><span>{mood}</span><span>{percentage}%</span></div>
        <div className="w-full bg-slate-200 rounded-full h-2.5 mt-1"><div className={`${colors[mood] || 'bg-gray-400'} h-2.5 rounded-full`} style={{ width: `${percentage}%` }}></div></div>
      </div>
    );
  };

  return (
    <div className="bg-white p-6 rounded-2xl shadow-lg">
      <h2 className="text-2xl font-bold text-slate-800 mb-4">Your Week</h2>
      <button onClick={handleShowSummary} className="w-full bg-indigo-600 text-white font-bold py-3 px-4 rounded-xl hover:bg-indigo-700 transition-colors">Generate My Weekly Summary</button>
      {showModal && summary && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4 animate-fade-in">
          <div className="bg-white rounded-lg shadow-2xl p-6 w-full max-w-md">
            <div className="flex justify-between items-center border-b pb-3 mb-4"><h2 className="text-2xl font-bold text-gray-800">Your Week in Review</h2><button onClick={() => setShowModal(false)} className="text-2xl font-bold text-gray-500 hover:text-gray-800">&times;</button></div>
            {summary.totalEntries > 0 ? (
              <div className="space-y-5">
                <div><h3 className="font-bold text-lg mb-2">Mood Breakdown</h3><div className="space-y-3"><MoodStat mood="POSITIVE" count={summary.moods.POSITIVE} total={summary.totalEntries} /><MoodStat mood="NEGATIVE" count={summary.moods.NEGATIVE} total={summary.totalEntries} /><MoodStat mood="NEUTRAL" count={summary.moods.NEUTRAL} total={summary.totalEntries} /></div></div>
                <div><h3 className="font-bold text-lg">Key Insights</h3><div className="mt-2 space-y-2"><p><strong>Your most common feeling was:</strong> <span className="font-semibold text-violet-700">{summary.topEmotion}</span></p><p><strong>Activity most linked to positive days:</strong> <span className="font-semibold text-amber-700">{summary.topActivity}</span></p></div></div>
                <div className="bg-green-50 p-3 rounded-lg border border-green-200"><p className="text-green-800"><strong>Pro-Tip:</strong> To boost your mood next week, try focusing on activities like **{summary.topActivity}**!</p></div>
              </div>
            ) : (<p className="text-center text-gray-600 py-8">Not enough data from the past week. Keep journaling to unlock your summary!</p>)}
          </div>
        </div>
      )}
    </div>
  );
}

// --- MoodChart.tsx ---
function MoodChart() {
  const [chartData, setChartData] = useState<any>({ datasets: [] });
  const [hasEnoughData, setHasEnoughData] = useState(false);

  useEffect(() => {
    if (typeof window === 'undefined') return;
    const history: HistoryEntry[] = JSON.parse(localStorage.getItem('aura-history') || '[]');
    const sevenDaysAgo = new Date();
    sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);
    const recentEntries = history.filter(entry => new Date(entry.date) >= sevenDaysAgo);

    if (recentEntries.length < 2) { setHasEnoughData(false); return; }
    setHasEnoughData(true);

    const labels = recentEntries.map(entry => new Date(entry.date).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }));
    const dataPoints = recentEntries.map(entry => (entry.sentiment === 'POSITIVE') ? 1 : (entry.sentiment === 'NEGATIVE') ? -1 : 0);

    setChartData({
      labels,
      datasets: [{ label: 'Mood Trend', data: dataPoints, borderColor: 'rgb(59, 130, 246)', backgroundColor: 'rgba(59, 130, 246, 0.5)', tension: 0.1 }],
    });
  }, []);

  const chartOptions: any = {
    responsive: true,
    plugins: { legend: { display: false }, title: { display: true, text: 'Your Mood Over the Last 7 Days' } },
    scales: { y: { ticks: { callback: (val: any) => (val === 1) ? 'Positive' : (val === -1) ? 'Negative' : (val === 0) ? 'Neutral' : null }, min: -1.5, max: 1.5 } },
  };

  if (!hasEnoughData) {
    return (
      <div className="bg-white p-6 rounded-2xl shadow-lg">
        <div className="text-center p-4 bg-slate-100 rounded-lg"><p className="text-slate-600">Journal for a few more days to see your mood trend chart!</p></div>
      </div>
    );
  }
  return (<div className="bg-white p-6 rounded-2xl shadow-lg"><Line options={chartOptions} data={chartData} /></div>);
}

// --- StreakTracker.tsx ---
function StreakTracker({ streak }: { streak: number }) {
  return (
    <div className="flex items-center gap-2 bg-white px-3 py-2 rounded-full shadow-sm border border-slate-200 animate-pop-in">
      <span className="text-2xl" role="img" aria-label="Streak flame">🔥</span>
      <span className="text-xl font-bold text-orange-500">{streak}</span>
    </div>
  );
}

// --- Helper Components from page.tsx ---
const InsightCard = ({ analysis }: { analysis: AnalysisResult }) => {
  const strategy = getPersonalizedStrategy(analysis);
  return (
    <div className="mt-6 bg-indigo-50 border border-indigo-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-indigo-800 flex items-center">{strategy.emoji} {strategy.title}</h3>
      <p className="text-indigo-700 mt-2">{strategy.text}</p>
    </div>
  );
};

const CorrelationCard = ({ analysis }: { analysis: AnalysisResult }) => {
  if (!analysis.activities || analysis.activities.length === 0) return null;
  let insightText = null;
  if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Exercise')) insightText = "Great job on exercising! It seems to have a positive impact on your mood.";
  else if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Social')) insightText = "Connecting with others is powerful. It looks like it boosted your spirits today!";
  else if (analysis.labels.includes('Fatigue') && analysis.activities.includes('Work')) insightText = "We noticed you logged 'Work' and are feeling fatigued. Remember to take regular breaks to recharge.";
  if (!insightText) return null;
  return (
    <div className="mt-4 bg-teal-50 border border-teal-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-teal-800 flex items-center">📈 Correlation Insight</h3>
      <p className="text-teal-700 mt-2">{insightText}</p>
    </div>
  );
};

const Tag = ({ label, type }: { label: string, type: 'sentiment' | 'label' | 'activity' }) => {
  const colorClasses: any = {
    sentiment: { POSITIVE: 'bg-green-100 text-green-800', NEGATIVE: 'bg-rose-100 text-rose-800', NEUTRAL: 'bg-slate-100 text-slate-800' },
    label: { Anxiety: 'bg-violet-100 text-violet-800', Fatigue: 'bg-slate-100 text-slate-800', Sadness: 'bg-blue-100 text-blue-800', Work: 'bg-amber-100 text-amber-800', Family: 'bg-pink-100 text-pink-800', default: 'bg-indigo-100 text-indigo-800' },
    activity: 'bg-amber-100 text-amber-800 border border-amber-200',
  };
  let color;
  if (type === 'sentiment') color = colorClasses.sentiment[label] || colorClasses.label.default;
  else if (type === 'label') color = colorClasses.label[label] || colorClasses.label.default;
  else color = colorClasses.activity;
  return (<span className={`px-3 py-1 text-sm font-medium rounded-full ${color}`}>{type === 'activity' && '🏃‍♂️ '}{label}</span>);
};

const TabButton = ({ icon, label, isActive, onClick }: { icon: React.ReactNode, label: string, isActive: boolean, onClick: () => void }) => (
  <button onClick={onClick} className={`w-full flex justify-center items-center space-x-2 px-3 py-2 rounded-md text-sm font-semibold transition-all ${isActive ? 'bg-white shadow-md text-indigo-700' : 'text-slate-600 hover:bg-slate-100'}`}>
    {icon}<span>{label}</span>
  </button>
);


// ==================================================================
// 3. MAIN PAGE COMPONENT
// ==================================================================
export default function HomePage() {
  const [journalText, setJournalText] = useState('');
  const [selectedActivities, setSelectedActivities] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [analysis, setAnalysis] = useState<AnalysisResult | null>(null);
  const [error, setError] = useState('');
  const [showBreathingExercise, setShowBreathingExercise] = useState(false);
  const [streak, setStreak] = useState(0);
  const [view, setView] = useState<View>('journal');

  useEffect(() => {
    if (typeof window === 'undefined') return;
    const today = getISODate(new Date());
    const yesterday = getISODate(new Date(Date.now() - 86400000));
    const streakData: StreakData = JSON.parse(localStorage.getItem('aura-streak') || '{"streakCount": 0, "lastJournalDate": null}');
    if (streakData.lastJournalDate === today) setStreak(streakData.streakCount);
    else if (streakData.lastJournalDate === yesterday) setStreak(streakData.streakCount);
    else if (streakData.lastJournalDate !== null) {
      setStreak(0);
      localStorage.setItem('aura-streak', JSON.stringify({ streakCount: 0, lastJournalDate: streakData.lastJournalDate }));
    } else setStreak(0);
  }, []);

  const handleActivityChange = (activity: string) => {
    setSelectedActivities(prev => prev.includes(activity) ? prev.filter(a => a !== activity) : [...prev, activity]);
  };

  const handleAnalyze = async () => {
    if (!journalText.trim()) { setError('Please write something in your journal first.'); return; }
    setIsLoading(true); setError(''); setAnalysis(null);
    try {
      const response = await axios.post('http://127.0.0.1:8000/analyze', { text: journalText, activities: selectedActivities });
      if (response.data.error) throw new Error(response.data.error);
      setAnalysis(response.data);

      const history = JSON.parse(localStorage.getItem('aura-history') || '[]');
      const newEntry = { date: new Date().toISOString(), sentiment: response.data.sentiment, labels: response.data.labels, activities: response.data.activities };
      history.push(newEntry);
      localStorage.setItem('aura-history', JSON.stringify(history));

      const today = getISODate(new Date());
      const yesterday = getISODate(new Date(Date.now() - 86400000));
      const streakData: StreakData = JSON.parse(localStorage.getItem('aura-streak') || '{"streakCount": 0, "lastJournalDate": null}');
      let newStreakCount = streakData.streakCount;
      if (streakData.lastJournalDate === today) { /* No change */ } 
      else if (streakData.lastJournalDate === yesterday) newStreakCount += 1;
      else newStreakCount = 1;
      localStorage.setItem('aura-streak', JSON.stringify({ streakCount: newStreakCount, lastJournalDate: today }));
      setStreak(newStreakCount);

    } catch (err) {
      console.error(err);
      setError('Sorry, we couldn\'t analyze your entry. The server might be busy. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      {showBreathingExercise && <BreathingExercise onClose={() => setShowBreathingExercise(false)} />}
      <main className="flex min-h-screen flex-col items-center bg-slate-50 font-sans">
        <header className="w-full max-w-2xl px-4 pt-6">
          <div className="flex justify-between items-center">
            <h1 className="text-3xl font-bold text-indigo-800">Aura Journal</h1>
            <StreakTracker key={streak} streak={streak} />
          </div>
        </header>
        <nav className="w-full max-w-2xl p-4 sticky top-0 z-10 bg-slate-50">
          <div className="flex justify-center space-x-2 rounded-lg bg-slate-200 p-1.5">
            <TabButton icon={<BookOpen size={18} />} label="Journal" isActive={view === 'journal'} onClick={() => setView('journal')} />
            <TabButton icon={<LayoutDashboard size={18} />} label="Dashboard" isActive={view === 'dashboard'} onClick={() => setView('dashboard')} />
          </div>
        </nav>
        <div className="w-full max-w-2xl p-4">
          {view === 'journal' && (
            <div className="animate-fade-in space-y-6">
              <div className="bg-white p-6 rounded-2xl shadow-lg">
                <textarea
                  value={journalText}
                  onChange={(e) => setJournalText(e.target.value)}
                  placeholder="How are you feeling today?"
                  className="w-full h-40 p-3 border-slate-200 border rounded-xl focus:ring-2 focus:ring-indigo-500 focus:border-indigo-500 transition-shadow duration-200"
                  disabled={isLoading}
                />
                <div className="mt-4">
                  <h3 className="text-md font-semibold text-slate-700 mb-2">What did you do today?</h3>
                  <div className="flex flex-wrap gap-2">
                    {ALL_ACTIVITIES.map(activity => (
                      <button key={activity} onClick={() => handleActivityChange(activity)} className={`px-3 py-1.5 text-sm font-medium rounded-full border transition-all ${selectedActivities.includes(activity) ? 'bg-indigo-600 text-white border-indigo-600' : 'bg-white text-slate-700 border-slate-300 hover:bg-slate-100'}`}>
                        {activity}
                      </button>
                    ))}
                  </div>
                </div>
                <button onClick={handleAnalyze} disabled={isLoading} className="w-full mt-6 bg-indigo-600 text-white font-bold py-3 px-4 rounded-xl hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 disabled:bg-slate-400 disabled:cursor-not-allowed transition-colors duration-200 shadow-md hover:shadow-lg">
                  {isLoading ? 'Analyzing...' : 'Analyze My Feelings'}
                </button>
              </div>
              {error && <p className="text-rose-500 text-center mt-4">{error}</p>}
              {analysis && (
                <div className="bg-white p-6 rounded-2xl shadow-lg animate-fade-in">
                  <h2 className="text-2xl font-bold text-slate-800 mb-4">Your Analysis</h2>
                  <div className="flex flex-wrap gap-2">
                    <Tag label={analysis.sentiment} type="sentiment"/>
                    {analysis.labels.map(label => <Tag key={label} label={label} type="label" />)}
                    {analysis.activities.map(activity => <Tag key={activity} label={activity} type="activity" />)}
                  </div>
                  <InsightCard analysis={analysis} />
                  <CorrelationCard analysis={analysis} />
                  {analysis.labels.includes("Anxiety") && (
                    <button onClick={() => setShowBreathingExercise(true)} className="w-full mt-4 bg-violet-600 text-white font-bold py-2 px-4 rounded-xl hover:bg-violet-700 transition-colors">
                      Try a Guided Breathing Exercise
                    </button>
                  )}
                </div>
              )}
            </div>
          )}
          {view === 'dashboard' && (
            <div className="animate-fade-in space-y-6">
              <MoodChart />
              <GoalTracker />
              <WeeklySummary />
            </div>
          )}
        </div>
      </main>
    </>
  );
}